body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

.jumbotron {
  background: -webkit-gradient(linear, left top, right top, from(lightgreen), to(lightblue));
  background: -webkit-linear-gradient(left, lightgreen, lightblue);
  background: -o-linear-gradient(left, lightgreen, lightblue);
  background: linear-gradient(to right, lightgreen, lightblue);
  border-radius: 0px !important;
  margin-top: -20px;
}

.searchForm {
  height: 80px;
  font-size: 50px;
}

.searchBtn {
  height: 80px;
  width: 200px;
}

.btn {
  margin-left: 5px;
}

.sortBtn {
  margin-right: 20px;
}

.navbar-default .navbar-nav > .active > a {
  background-color: lightblue !important;
}



